import { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Service from "../../../API/Service";
import { useFetching } from "../../../utils/hooks/useFetching";
import DeviceItem from "./DeviceItem";
import { Button, CircularProgress, Dialog, DialogActions, DialogTitle, Grid, Typography } from "@mui/material";
import TableItems from "./TableItems";
import MainCard from "components/MainCard";
import { useQuery } from "react-query";

function DeviceList() {
    const { state } = useLocation();
    const [open, setOpen] = useState(false);
    const [name, setName] = useState('');
    const [devices, setLastDevices] = useState([]);
    const [id, setId] = useState(null);
    const [programPacks, setProgramPacks] = useState([]);
    const [fetchDevices] = useFetching(async () => {
        const response = await Service.getDevices(state);
        setLastDevices(response.devices);
    });

    const { isLoading: isTableLoading, error: TableError, refetch } = useQuery(
        ['programPack', id], () => Service.getProgramPacks(id),
        {
            select: (data) => {
                return data?.programPack
            },
            onSuccess: (data) => {
                setId(null);
                setProgramPacks(data);
            },
            refetchOnWindowFocus: false,
            enabled: !!id
        }
    );

    const handleClickOpen = (id, name) => {
        setOpen(true);
        setName(name);
        setId(id);
    };

    const handleClose = () => {
        setOpen(false);
    };

    useEffect(() => {
        fetchDevices();
    }, []);
    return (
        <>
            {
                devices.length > 0
                    ?
                    devices.map((device) => {
                        return <DeviceItem onOpen={handleClickOpen} device={device} />;
                    })
                    :
                    <Grid item xs={14} sm={8} md={5} lg={4}>
                        <MainCard contentSX={{ p: 2.25 }} >
                            Нет подключения к устройствам или нет активных устройств
                        </MainCard>
                    </Grid>
            }
            <Dialog maxWidth={"xl"} onClose={handleClose} open={open}>
                <DialogTitle>
                    <Typography variant="h2">{name}</Typography>
                </DialogTitle>
                {isTableLoading || programPacks.length === 0
                    ?
                    <Grid container justifyContent={'center'}>
                        <CircularProgress />
                    </Grid>
                    :
                    TableError
                        ?
                        <p>Ошибка </p>
                        :
                        <TableItems rows={programPacks} />
                }

                <DialogActions >
                    <Button variant="outlined" onClick={handleClose}>Закрыть</Button>
                </DialogActions>
            </Dialog>
        </>
    );
}
export default DeviceList;