import { Box, Button, Divider, Grid, Stack, Typography } from "@mui/material";
import MainCard from "components/MainCard";



function DeviceItem({ device, onOpen }) {
    return (
        <Grid item xs={14} sm={8} md={5} lg={4}>
            <MainCard contentSX={{ p: 2.25 }} >

                <Box>
                    <Grid container sx={{ mb: 1.5 }}>
                        <Grid item xs>
                            <Typography variant="h3">{device.name || 'Название не задано'}</Typography>
                        </Grid>
                    </Grid>
                </Box>
                <Divider component="div" primary="dark" />
                <Grid sx={{ mt: 1.5 }} >
                    <Stack spacing={1}>
                        <Typography variant="h6">{"ip: " + device.ip}</Typography>
                        <Typography variant="h6">{"Порт: " + device.port}</Typography>
                        <Typography variant="h6">{"Протокол: " + device.interfaceConnect}</Typography>
                    </Stack>
                    <Button onClick={() => onOpen(device._id, device.name)}>Таблица тегов</Button>
                </Grid>
            </MainCard>
        </Grid >
    );
}

export default DeviceItem;